import React,{useEffect, useState} from 'react'
import axios from 'axios'

export const Useradd = () =>{
    return(
        <>
        </>
    )
}

export const Userview = () =>{
    return(
        <>
        </>
    )
}

function User() {

    const [ item, setItem ] = useState('')
    const [ error, setError ] = useState('')
    const [ alreadyItem, setAlreadyItem ] = useState('')
    const [ itemlist, setItemList ] = useState(['ritesh'])
    

    const AddItem = (e) =>{
        e.preventDefault();
    
        if(item === ''){
            setError('error')
        }else{
            
            
            itemlist.filter( (elem, ind)=>{
                console.log(elem);
                console.log(item);
                
                if(item === elem){
                    console.log('inside')
                    setAlreadyItem('<p className="error-text">Already Item Added</p>')
                    
                }
            })
            console.log(alreadyItem);
            if(alreadyItem === ''){
                setItemList([...itemlist, item])
                setItem('')
                setAlreadyItem('')
            }
            
        }
        
    }

    const deleteItem = (index) =>{
        console.log(index);
        const updateitem = itemlist.filter( (elem, ind)=>{
            return index !== ind
        })
        console.log(updateitem)
        setItemList([...itemlist, item])
        setItemList(updateitem)
    }
    console.log(itemlist);

    useEffect(() => {
        axios.get('https://jsonplaceholder.typicode.com/posts')
        .then( (response)=>{
            if(response.status == 200){
                let titlearra = [];

                const onlytitle = response.data.filter( (arr, index)=>{
                    console.log(arr.title);
                    titlearra[index] = arr.title;
                })
                setItemList(titlearra)
                console.log(titlearra)

            }
        })
        .catch( (error)=>{
            console.error(error)
        })

    }, [])

    return (
        <>
        <div className='row'>
        <div className='col-md-4'></div>
        <div className='col-md-4'>
            <div className="card">
                <div className="card-body">
                <h4 className="card-title">Add User</h4>
                <div>
                    <form onSubmit={ AddItem }>
                        <div className="form-group">
                            <input type="text" name='item' className={`form-control ${error}`} onChange={ (e)=>{ 
                            setItem(e.target.value) 
                            setError('')

                            }} placeholder="Enter item.." id="item" value={item} style={{    width: '90%', display: 'inline'}} />
                            <button type="submit" style={{padding: '7px',border: 'none',borderRadius: '2px'}}>Add</button>
                            {alreadyItem}
                        </div>
                    </form>
                </div>
                <br/>
                <div>
                    <h3 className="text-uppercase">USER List</h3>
                    <table className="table table-hover">
                    <thead>
                    <tr>
                        <th>Sl.no.</th>
                        <th>Item Name</th>
                        <th className='text-center'>Action</th>
                    </tr>
                    </thead>
                    <tbody>
                    { itemlist.map( (items, index)=>(
                        <tr key={index}>
                            <td>{index+1}</td>
                            <td>{items}</td>
                            <td className='text-center'>
                                <button className="btn btn-danger btn-sm" onClick={ ()=>{ deleteItem(index) }}>X</button>
                            </td>
                        </tr>

                    ))}
                    
                    </tbody>
                </table>
                </div>
                
                </div>
            </div> 
        </div>
        
        </div>
        
        </>

    )
}

export default User
