import React, { useState,useEffect } from 'react'
import axios from 'axios'
import { additemtocart } from '../../featured/cart/cart-slice'
import { useDispatch, useSelector } from 'react-redux';
 

const Product= () => {
    let newitem = [];
        
    const [productlist, setProductList] = useState([])
    const dispatch = useDispatch();
  


    useEffect(() => {
        axios.get('http://127.0.0.1:8000/api/product').then( (response)=>{
            console.log(response.data.data)
            setProductList(response.data.data)
        }).catch( (error)=>{
            console.log(error)
        })

    }, [])


    {/* cart add */}

    const addToCart = (elem) =>{
        dispatch(additemtocart(elem));
    }

    return (
        <>
        { productlist.map( (product)=>(
            
        <div className="col-md-3" key={product.sku}>
            <div className="card">
                <img className="card-img-top" src="https://via.placeholder.com/150" alt={product.product_name} style={{width:'100%'}}  />
                <div className="card-body">
                <h4 className="card-title">{product.product_name}</h4>
                <p className="card-text">{product.product_description}</p>
                <p className="card-text">Price: {product.selling_price}</p>
                <a href="#" className="btn btn-primary">View</a>
                &nbsp;
                <button type="button" className="btn btn-primary" onClick={ () =>addToCart(product) }>Add To cart</button>
                </div>
            </div>
        </div>
        
        )) }
        </>
    )
}

export default Product