import React,{ useContext, useState } from 'react'
import { IsAlert } from '../../Context'

export default function SuccessAlert(props) {
    const Islert = useContext(IsAlert)

    return (
        
        <div className={`alert alert-${props.alert} alert-dismissible`}>
            <button type="button" className="close" data-dismiss="alert" onClick={props.CloseAlert}>&times;</button>
            <strong>{props.alert} ! </strong> {props.msg}.
        </div>
        
    )
}
