import React, { useState, useEffect } from "react";
import { useDispatch,useSelector } from "react-redux";
import {deleteItemFromCart,incrementItemQty,getCartList,decrementItemQty} from './cart-slice'
import { NavLink } from 'react-router-dom'


const CartProduct = () => {
  const cartitem = useSelector(getCartList)
  
  const [cartTotal, setCartTotal] = useState(0);
  
  const dispatch = useDispatch();

  const deleteItem = (productsku) =>{
    const status = dispatch(deleteItemFromCart(productsku))
    console.log(status)
  }
  useEffect(() => {
    let total = 0;
    cartitem.filter((cart)=>{
      return total += parseInt(cart.selling_price) * parseInt(cart.pqty)
    })
    setCartTotal(total)
  }, [cartitem])


  return (
    <>
      <div className="row text-center">
        <div className="col-md-2"></div>
        <div className="col-md-8">
          <div className="card">
            <h4 className="card-title">Cart</h4>
            <div className="card-body">
              <table className="table table-striped">
                <thead>
                  <tr>
                    <th>Sl.no</th>
                    <th>Product name</th>
                    <th>Qty</th>
                    <th>price</th>
                    <th>Total</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>

                  {cartitem.map((cart, index) => (
                    <tr key={index}>
                      <td>{index+1}</td>
                      <td>{cart.product_name}</td>
                      <td>
                        <button className="btn btn-sm btn-danger" onClick={ ()=>dispatch(decrementItemQty(cart.sku)) }>-</button>
                        &nbsp;
                        {cart.pqty}
                        &nbsp;
                        <button className="btn btn-sm btn-primary" onClick={ ()=>dispatch(incrementItemQty(cart.sku))}>+</button>
                      </td>
                      <td>Rs. {cart.selling_price}</td>
                      <td> RS. {cart.selling_price * cart.pqty}</td>
                      <td><button onClick={()=>deleteItem(cart.sku)}>X</button></td>
                    </tr>
                  ))}
                  {
                  (cartitem.length > 0)?
                  <tr>
                    <td colSpan="5" className="text-right">
                      Total
                    </td>
                    <td> RS. {cartTotal}</td>
                  </tr>
                  :
                  <tr>
                    <td colSpan="6" className="text-center">
                      
                      <NavLink exact activeClassName='active' to='/'  className=" btn btn-info">Empty cart Go to shopping</NavLink>
        
                    </td>
                  </tr>

                  }

                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CartProduct;
