import React,{useState} from 'react'

function Calculator() {
    const [num1, setNum1] = useState(0)
    const [num2, setNum2] = useState(0)
    const [total, setTotal] = useState(0)
    const [showString, setShowString] = useState(0)
    const [operator, setOperator] = useState('')
    
    const setNumber = (num) =>{
        // alert(num1);
        if(operator === ''){
            // const whole = num1.concat(num);
            // const intwhole = parseInt(whole);
            setNum1(num);
            setShowString(num);

        }else{

        }
    }


    return (
        <>
        <div className='row'>
        <div className='col-md-4'></div>
        <div className='col-md-4'>
            <div className="card">
                <div className="card-body">
                    <h4 className="card-title">Mini calculator</h4>
                    <table className="table table-hover mini-calc">
                        <thead>
                        <tr>
                            <td colSpan="4">
                                <input type="text" name='item' className="form-control" placeholder="Enter number.." id="item" value={showString} disabled  />
                            </td>
                        </tr>
                        </thead>
                        <tbody>
                        <tr>
                            <td>
                                <button type="button" className="form-control btn btn-info" onClick={ () => { setNumber(1) } } style={{padding: '7px',border: 'none',borderRadius: '2px'}}>1</button>
                            </td>
                            <td>
                                <button type="button" className="form-control btn btn-info" onClick style={{padding: '7px',border: 'none',borderRadius: '2px'}}>2</button>
                            </td>
                            <td>
                                <button type="button" className="form-control btn btn-info" onClick style={{padding: '7px',border: 'none',borderRadius: '2px'}}>3</button>
                            </td>
                            <td>
                                <button type="button" className="form-control btn btn-info" onClick style={{padding: '7px',border: 'none',borderRadius: '2px'}}>+</button>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                
                </div>
            </div> 
        </div>
        
        </div>
        
        </>
    )
}

export default Calculator
