import React from 'react'
import Product from '../Component/common/Product'


const Home = () => {
    
    return (
        <>

        <h1 className="display-4">Your are on HOME!</h1>
        <p className="lead">This is a simple Example of using 
        bootstrap in React.</p>

        <hr className="my-4" />

        
        {/* product controller */}
        <div className="row">
            <div className="col-md-12 text-center text-uppercase">
                <h1>Product</h1>
            </div>
        </div>
        <div className="row">
            <Product />
        </div>

        {/* Carousel */}

        <div className='carousel-box'>
            
            <div id="demo" className="carousel slide" data-ride="carousel">

                <ul className="carousel-indicators">
                    <li data-target="#demo" data-slide-to="0" className="active"></li>
                    <li data-target="#demo" data-slide-to="1"></li>
                    <li data-target="#demo" data-slide-to="2"></li>
                </ul>
                
                <div className="carousel-inner">
                    <div className="carousel-item active">
                    <img src="https://via.placeholder.com/150" alt="Los Angeles" width="300"  />
                    </div>
                    <div className="carousel-item">
                    <img src="https://via.placeholder.com/150" alt="Chicago" width="300" />
                    </div>
                    <div className="carousel-item">
                    <img src="https://via.placeholder.com/150" alt="New York" width="300" />
                    </div>
                </div>
                
                <a className="carousel-control-prev" href="#demo" data-slide="prev">
                    <span className="carousel-control-prev-icon"></span>
                </a>
                <a className="carousel-control-next" href="#demo" data-slide="next">
                    <span className="carousel-control-next-icon"></span>
                </a>
                </div>
        </div>
        

        </>
    )
}

export default Home 