import React, {useContext} from 'react'
import { NavLink } from 'react-router-dom'
import authContext from '../Context'

import { useSelector } from 'react-redux'
import {selectCount } from '../featured/counter/counter-slice'
import { getCartQuantity } from '../featured/cart/cart-slice'

function Navbar() {

    const { setAuthenticated } = useContext(authContext);
    const handleLogin = () => setAuthenticated(true);
    const handleLogout = () => setAuthenticated(false);
    const count = useSelector(selectCount);
    const cartQuantity = useSelector(getCartQuantity);
  


    return (
        <nav className="navbar navbar-expand-sm bg-dark navbar-dark">
            <NavLink activeClassName='active' to='/'  className="navbar-brand">ApnaBlog</NavLink>
        <ul className="navbar-nav">
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/'  className="nav-link">Home</NavLink>
            </li>
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/about'  className="nav-link">About</NavLink>
            </li>
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/blog'  className="nav-link">Blog</NavLink>
            </li>
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/contact'  className="nav-link">Contact</NavLink>
            </li>
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/todo'  className="nav-link">Todo</NavLink>
            </li>
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/user'  className="nav-link">User</NavLink>
            </li>
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/calculator'  className="nav-link">Calculator</NavLink>
            </li>

            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/counter'  className="nav-link">Counter redux ({count})</NavLink>
            </li>
            
            <li className="nav-item">
                <NavLink exact activeClassName='active' to='/cart'  className="nav-link">Cart ({cartQuantity})</NavLink>
            </li>
            
            

        </ul>
        </nav>
    )
}

export default Navbar
