import React,{useEffect, useState} from 'react'
import axios from 'axios'
import SuccessAlert from './Alert'


export const BlogAdd = () =>{
    
    const [ title, setTitle ] = useState('')
    const [ content, setContent ] = useState('')
    const [isAlert, setIsAlert] = useState(false)
    const [isLoader, setIsLoader] = useState(true)
    
    {/* submit a blog*/}
    
    const submitBlog = (e) =>{
        e.preventDefault();

        {/* loader intilize */}
        setIsLoader(false)  

        const postData = {
            title:title,
            body:content,
            userId: 1,
        }

        axios.post(`https://jsonplaceholder.typicode.com/posts`, { postData })
        .then( (res)=>{
            // setBlog(res)
            setIsAlert(true)
            setIsLoader(true)
        })

    }

    {/* Alert close function and efefct */}

    const CloseAlert = () =>{
        setIsAlert(false)
    }

    useEffect( ()=>{
        console.log('Islaert effect');
    },[isAlert])

    var alertMsg = '';
    
    if(isAlert){
        alertMsg = <SuccessAlert alert='success' msg='blog created successfully' CloseAlert={CloseAlert} />
    }

    {/* Loader hide and show*/}
    let LoaderMsg='';
    if(isLoader){
        LoaderMsg = 'Submit'
    }else{
        LoaderMsg = 'Loading....'
    }
    

    return(
        
        <>
        
        {alertMsg}

        <form onSubmit={ submitBlog }>
        <div className="form-group">
            <label htmlFor="title">Title:</label>
            <input type="text" name='title' className="form-control" onChange={ (e)=>{ console.log(e.target.value)
            setTitle(e.target.value) 
            }} placeholder="Enter title" id="title" />
        </div>
        <div className="form-group">
            <label htmlFor="pwd">Content:</label>
            <textarea name='content' placeholder="enter blog content here.." onChange={ (e)=>{ setContent(e.target.value) }} className="form-control"></textarea>
        </div>
        <button type="submit" className="btn btn-primary">{LoaderMsg}</button>
        </form>

        </>
    )



}

const BlogCardList = (props) => {
    return (
        <div className="card">
            <img className="card-img-top" src="https://via.placeholder.com/150" alt={props.blog.title} style={{width:'100%'}}  />
            <div className="card-body">
            <h4 className="card-title">{props.blog.title}</h4>
            <p className="card-text">{props.blog.body}</p>
            <a href="#" className="btn btn-primary">View Blog</a>
            </div>
        </div>
    )
}


export default BlogCardList


