import {useState} from 'react'

import './App.css'

import { Route, Switch } from 'react-router-dom'
import  authContext  from './Context'


import Layout from './Layout'

import Home from './pages/Home'
import About from './pages/About'
import Contact from './pages/Contact'
import Blog from './pages/Blog'
import Todo from './pages/Todo'
import Calculator from './pages/Calculator'

import User, { Useradd, Userview} from './pages/User'

import { Counter } from './featured/counter/counter'
import Cart from './featured/cart/cart'


function App() {
  
  const [authenticated, setAuthenticated] = useState(false);

  return (
    <>
    <authContext.Provider value={false}>
    <Layout>
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/about' render={About} />
          <Route exact path='/contact' render={ ()=> <Contact name='contact us' /> } />  {/* if we use passs props in route component */}
          <Route exact path='/blog' render={ ()=> <Blog /> } />  {/* if we use passs props in route component */}
          <Route exact path='/todo' render={ ()=> <Todo /> } />  {/* if we use passs props in route component */}
          <Route exact path='/user' render={ ()=> <User /> } />  {/* if we use passs props in route component */}
          <Route exact path='/user/view/:id' render={ ()=> <Userview /> } />  {/* if we use passs props in route component */}
          <Route exact path='/user/add' render={ ()=> <Useradd /> } />  {/* if we use passs props in route component */}
          <Route exact path='/calculator' render={ ()=> <Calculator /> } />  {/* if we use passs props in route component */}
          <Route exact path='/counter' render={ ()=> <Counter /> } />  {/* if we use passs props in route component */}
          <Route exact path='/cart' render={ ()=> <Cart /> } />  {/* if we use passs props in route component */}
          </Switch>
      </Layout>
      </authContext.Provider>
    </>
  );
}

export default App;
